import React  from 'react';
import imgPerfil from '../imagenes/user.svg';

import "./InicioSesion.css";


export default function InicioSesion() {  
  // const [login,setLogin]=useState({
  //   mail:"",
  //   password:""
  // });
  const functAutenticacion= async (e)=>{
    e.preventDefault();
    const correo=e.target.email.value;
    const password=e.target.password.value;
    console.log(correo + " "+ password)
  };

  return (
    <div class="Container">
        <section class="Box" >
            <img class="Img_Perfil" src={imgPerfil} alt="Imagen_perfil" />
            <form onSubmit={functAutenticacion}>
              <input className='Place'
              type="email"
              placeholder= 'email@ejemplo.com'
              id="email"
              />
              
              <input className='Place'
              type="password"
              placeholder= 'Contrasea...'
              id="password"
              />
              
              <input  className='Place Boton'
              type="submit"
              value="Iniciar Sesin"
              /> 
            </form>
            <div>
              <p>Al continuar, aceptas las Condiciones de uso y el Aviso de privacidad de Crazy Games.</p>
              <p>Eres nuevo en Crazy Games?</p>
              <input  className='Registro'
                type="submit"
                value="Registrarse"
                /> 
            </div>     
        </section> 
    </div>
  )
}